:root {
  --tile-size: min(calc((100vw - 1rem * 2)  / 8), calc((100vh - 6rem * 2)  / 8) );
  --accent-color: #fcd53f;
}

body {
  height: 100vh;
  box-sizing: border-box;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: max(3rem, env(safe-area-inset-top) );
  padding-bottom: max(3rem, env(safe-area-inset-bottom) );
  background: url("background.fd88afd0.jpg") center / cover;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  gap: 2rem;
  margin: 0;
  font-family: Saira Semi Condensed, sans-serif;
  font-weight: bold;
  display: flex;
}

.container {
  position: relative;
}

.statistics {
  width: 100%;
  max-width: calc(var(--tile-size, 8em)  * 8);
  color: var(--accent-color);
  text-shadow: -2px 2px #19253d75;
  font-size: 1.5rem;
  font-weight: bold;
}

.statistics > ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.multiplier, .game-over {
  z-index: 1;
  pointer-events: none;
  opacity: 0;
  color: var(--accent-color);
  text-shadow: -4px 4px 4px #3a3a3a;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  inset: 0;
}

.multiplier {
  font-size: 15rem;
}

.game-over {
  cursor: pointer;
  background-color: #34312f82;
  border: .5rem double;
  border-radius: .5rem;
  font-size: 4rem;
  transition: opacity .3s cubic-bezier(.4, 0, .2, 1);
}

.board {
  width: var(--board-width, 8em);
  height: var(--board-height, 8em);
  font-size: var(--tile-size, 8em);
  background-color: #2f4f4f99;
  border: .5rem double #21201e85;
  border-radius: .5rem;
  position: relative;
  box-shadow: inset -4px 4px 18px #4a4a4a;
}

.tile {
  cursor: pointer;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  text-shadow: -2px 4px 8px #525252;
  justify-content: center;
  align-items: center;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.tile.active {
  outline: 5px ridge var(--accent-color);
  z-index: 1;
}

.tile:before {
  content: "";
  width: 1em;
  height: 1em;
  background-size: cover;
}

.tile.crystal:before {
  background-image: url("crystal.18a77376.png");
}

.tile.mushroom:before {
  background-image: url("mushroom.c16d5ba6.png");
}

.tile.pumpkin:before {
  background-image: url("pumpkin.12766bca.png");
}

.tile.scull:before {
  background-image: url("scull.723562c5.png");
}

.tile.stone:before {
  background-image: url("stone.0f04f26a.png");
}

.tile.hat:before {
  background-image: url("hat.3efde180.png");
}

.tile.flask:before {
  background-image: url("flask.6c9424bf.png");
}

